import { Heading, Button, Image, Flex, Box, Text,Container } from "@chakra-ui/react";
import resume from "../Images/resume.pdf";
import React from "react";
import mypic from "./mypic.jpg";
import "./about.css";
const About = () => {
  function redirect() {
    window.open(
      "https://drive.google.com/file/d/1zztPdp7ZZE-dTh7X4-V7oOVyuHH7TVXH/view",
      "_blank",
      "noreferrer"
    );
  }
  return (
    <div>
      {/* <div>
      <Heading style={{ margin: "20px" }} color={"red.600"}>
        About 
      </Heading>
      </div> */}

      <div id="about" class="about section" style={{paddingTop:"120px"}} >
        <div id="imageDiv" style={{backgroundColor:"black"}}>
          <img className="home-img" src={mypic} />
        </div>
        <div style={{borderRadius:"30px",backgroundColor:"black",border:"4px solid #2e9b56"}} >
          <Heading style={{ margin: "20px" }} color={"white"}>
            About
          </Heading>
          {/* <Heading color={"blue.600"} id="user-detail-name">
            Hi , I am Sachin Kesarwani
            <Heading>
              <img
                style={{ margin: "auto" }}
                src="https://readme-typing-svg.demolab.com?font=Fira+Code&size=30&pause=1000&color=BC3131&background=FFFFFF3B&width=435&lines=Full+Stack+Web+Developer"
                alt="Typing SVG"
              />
            </Heading>
          </Heading> */}
          <Container maxW='md' color='white'>
          <Text  textAlign={"justify"} fontSize={"lg"}  id="user-detail-intro">
          Highly motivated and eager to learn new things.
Hard-working , creative , proactive , and specialized in HTML , CSS ,JavaScript and Javascript framework
React JS for frontend  and MongoDB , ExpressJS and NodeJS for  backend.  Keep improving skill day by day.
Ability to adapt in both self-starting and collaborative
environments.
          </Text>
  </Container>
         
          <a
            href={resume}
            id="resume-link-2"
            target="_blank"
            download="Sachin-Kesarwani.pdf"
          >
            <Button
              style={{ position: "static", marginTop: "20px" }}
           onClick={redirect}
            bg="black"
              _hover={{ bg:"#2e9b56",color:"white"}}
              color="#2e9b56"
           border={"2px solid #2e9b56"}
              id="resume-button-2"
            >
              Resume
            </Button>
          </a>
        </div>
      </div>
    </div>
  );
};

export default About;
