import React from 'react'
import worldBook from "../Images/worldBook.jpg"
import lavishLook from "../Images/lavishLook.jpg"
import monday from "../Images/monday.jpg"
import fitfactory from "../Images/fitfactory.jpg"
import { Badge, Button, Heading ,Text} from '@chakra-ui/react'
import "./project.css"
import { Link } from 'react-router-dom'
let data=[
  {id:1,name:"World Book",img:worldBook,link:"https://poetic-griffin-07f79c.netlify.app/",git:"https://github.com/Sachin-Kesarwani/smooth-cracker-7739",
   description:"This website generally for to buy books , the main reason behind to built this website is make students life easy and relaxe . ",tech:["HTML" , "CSS" , "JavaScript"]},
  {id:2,name:"Lavish Look",img:lavishLook,link:"https://lavish-look-sachin-kesarwani.vercel.app/",git:"https://github.com/Sachin-Kesarwani/pushy-dime-8312",
description:"This is E-commerce websit where you can buy clothes , shoes and products for kids . In this website there are many types product for both mens , womens and for childrens.",tech:["REACT" , "Chakra UI" , "Bootstrap"]},
  {id:3,name:"monday.com",img:monday,link:"https://lovely-snickerdoodle-8bb2d5.netlify.app/",git:"https://github.com/lokeshchoudharyprogrammer/superficial-point-8316",tech:["HTML"  , "CSS" , "JavaScript"],
description:"This is a collaborative which is cloned by 5 developers . Basically This website is just for to make team ,assign member for team ."}, 
{id:4,name:"Fit-Factory",img:fitfactory,link:"https://fit-factory.vercel.app/",git:"https://github.com/Sachin-Kesarwani/detailed-hour-2985",tech:["React" ,  "Chakra UI" ,"Redux", "React-Redux", "Redux-thunk"],
description:"This is a collaborative which is cloned by 3 developers . Basically This is the Indias No 1website where we sell health realted products ."}
]
const Project = () => {
  return (
    <div style={{paddingTop:"120px"}}>
        <Heading margin={"20px"}  color={"white"}>My Projects</Heading>
 <div  id="projects">
     
      {/* <div id="projectmaindiv"  > */}
        {
          data.map((e)=>{
            return <div style={{borderRadius:"10px",backgroundColor:"black",border:"3px solid #2e9b56"}}   className="project-card" >
                <div id="projectimageDiv"  style={{height:"48%"}} >
                  <Heading as={"h4"} color={"#2e9b56"} className="project-title">{e.name}</Heading>
                  <img id="projectimage" src={e.img} />
                </div>
                {/* <hr style={{border:"1px solid black"}}></hr> */}
                <div style={{padding:"8px",height:"30%"}} >
                 <Text color='white' textAlign={"justify"}  className="project-description">{e.description}</Text>

                </div>

                {/* <hr style={{border:"1px solid black"}}></hr> */}
                <div className="project-tech-stack" >
               <Text >
                <Badge bg="blue.400" color={"white"} >
                Tech Stack
                </Badge>
                  {e.tech.map((e)=><Badge margin={"0px 3px" } variant='solid' colorScheme='orange'>{e}</Badge>)}</Text>
                </div>
                {/* <hr style={{border:"1px solid black"}}></hr> */}
                <div>
                  <Button style={{position: "static"}}  className="project-deployed-link"   bg="black"
              _hover={{ bg:"#2e9b56",color:"white"}}
              color="#2e9b56"
           border={"2px solid #2e9b56"} margin={1}   ><a href={e.link}  target="_blank">Live </a></Button>
                  <Button style={{position: "static"}} className="project-github-link"  bg="black"
              _hover={{ bg:"#2e9b56",color:"white"}}
              color="#2e9b56"  border={"2px solid #2e9b56"}  ><a href={e.git}  target="_blank">Github</a></Button>
                </div>
            </div>
          })
        }
      {/* </div> */}
    </div>
    </div>
   
  )
}

export default Project
